import React from 'react'

const CardArmor = (props) => {
	const { armor, index, handlerChangeArmor } = props;
	return (
		<div key={'armor_num' + index} onClick={() => handlerChangeArmor(index)} >
			{armor.imgSrc ?
				<>
					< img src={armor.imgSrc} alt={armor.name} />
				</>
				:
				<button onClick={() => handlerChangeArmor(index)}>Очистить</button>
			}
		</div>
	)
}
export default CardArmor;
