import React, { Component } from 'react'
import left from "./img/left.png";
import right from "./img/right.png";
import sword from "./img/sword.png";

import CardClothes from './CardClothes';
import CardArmor from './CardArmor';
import CardWonderfull from './CardWonderfull';

export default class DianaGirlOuter extends Component {
	constructor(props) {
		super(props);
		this.state = {
			select_winter_dress: 0,
			select_dress: 0,
			select_armor: [],
			armor: [
				{ name: "none", imgSrc: null, className: "none", },
				{ name: "left", imgSrc: left, className: "left", },
				{ name: "right", imgSrc: right, className: "right", },
				{ name: "sword", imgSrc: sword, className: "sword", },
			]

		}
	}

	/**
	 * без bind получаем index и запускаем сохранение выбранных картинок
	 */
	handlerChangeDress = (index) => {
		console.log("костюм ", index);
		this.setState({ select_dress: index });
	}

	handlerChangeArmor = (index) => {
		console.log("бронь ", index);
		if (!index) {
			this.setState({ select_armor: [] });
			return
		}
		let select_armor = this.state.select_armor;
		select_armor = select_armor.includes(index)
			? select_armor.filter(a => a !== index)
			: [...select_armor, index];
		this.setState({ select_armor: select_armor });
	}

	render() {
		return (
			<div className='outer-wrapper'>
				<div className='dress-wrapper' >

					{/* блок со шмотками */}
					<div className='dress-card-wrapper'>
						{this.props.season.map((dress, index) =>
							<CardClothes
								key={'dress_num' + index}
								handlerChangeDress={this.handlerChangeDress}
								dress={dress}
								index={index}
							/>
						)}
					</div>

					{/* блок с главной картинкой */}
					<CardWonderfull
						key={'main'}
						dress={this.props.season[this.state.select_dress]}
						armor={this.state.armor}
						select_armor={this.state.select_armor}
					/>

					{/* блок с броней */}
					<div className='dress-card-wrapper'>
						{this.state.armor.map((armor, index) =>
							<CardArmor
								key={'armor_num' + index}
								handlerChangeArmor={this.handlerChangeArmor}
								armor={armor}
								index={index}
							/>
						)}
					</div>

				</div>
			</div>
		)
	}
}
