import React from 'react'
import mainPhoto from "./img/diana.png";

const CardWonderfull = (props) => {
	const { armor, dress, select_armor } = props;

	return (
		<div key={'main'} className='main-photo-wrapper'>
			<img src={mainPhoto} alt="" />

			<div className='dressed-clothes'>
				<img className='selected-clothes' src={dress.imgSrc} alt="" />
			</div>
			{select_armor.map((armor_num, index) =>
				<div key={'select' + armor_num + index} id={armor[armor_num].className}>
					<img src={armor[armor_num].imgSrc} alt="" />
				</div>
			)
			}
		</div>

	)


}

export default CardWonderfull;
