import React, { Component } from 'react'
import DianaGirlOuter from './ren/DianaGirlOuter';
import './main-style.css';
import CardSeason from './CardSeason';

import springWonder from './ren/img/spring/wonder-spring.png';
import springBlack from './ren/img/spring/black-spring.png';
import springBlue from './ren/img/spring/blue-spring.png';

import summerWonder from './ren/img/summer/wonder-summer.png';
import summerBlack from './ren/img/summer/black-summer.png';
import summerBlue from './ren/img/summer/blue-summer.png';

import autumnWonder from './ren/img/autumn/wonder-autumn.png';
import autumnBlack from './ren/img/autumn/black-autumn.png';
import autumnBlue from './ren/img/autumn/blue-autumn.png';

import winterWonder from './ren/img/winter/wonder-winter.png';
import winterBlack from './ren/img/winter/black-winter.png';
import winterBlue from './ren/img/winter/blue-winter.png';

export default class MainComponent extends Component {

	constructor(props) {
		super(props);
		this.state = {

			selected_season: 'blue',
			seasons: [
				{ season: 'spring', color: 'yellow' },
				{ season: 'summer', color: 'lime' },
				{ season: 'autumn', color: 'tomato' },
				{ season: 'winter', color: 'lightblue' },
			],
			clothes_spring: [
				{ name: "none", imgSrc: null, className: "none", },
				{ name: "springWonder", imgSrc: springWonder, className: "springWonder", },
				{ name: "springBlack", imgSrc: springBlack, className: "springBlack", },
				{ name: "springBlue", imgSrc: springBlue, className: "springBlue", }
			],
			clothes_summer: [
				{ name: "none", imgSrc: null, className: "none", },
				{ name: "summerWonder", imgSrc: summerWonder, className: "summerWonder", },
				{ name: "summerBlack", imgSrc: summerBlack, className: "summerBlack", },
				{ name: "summerBlue", imgSrc: summerBlue, className: "summerBlue", }
			],
			clothes_autumn: [
				{ name: "none", imgSrc: null, className: "none", },
				{ name: "autumnWonder", imgSrc: autumnWonder, className: "autumnWonder", },
				{ name: "autumnBlack", imgSrc: autumnBlack, className: "autumnBlack", },
				{ name: "autumnBlue", imgSrc: autumnBlue, className: "autumnBlue", }
			],
			clothes_winter: [
				{ name: "none", imgSrc: null, className: "none", },
				{ name: "winterWonder", imgSrc: winterWonder, className: "winterWonder", },
				{ name: "winterBlack", imgSrc: winterBlack, className: "winterBlack", },
				{ name: "winterBlue", imgSrc: winterBlue, className: "winterBlue", }
			],
		}
	}
	handlerSeason = (season) => {
		// console.log(season);
		this.setState({ selected_season: season });
	}

	getSeason() {
		let season = '';
		switch (this.state.selected_season) {
			case 'spring':
				// console.log('spring');
				return season = this.state.clothes_spring;
			case 'summer':
				// console.log('summer');
				return season = this.state.clothes_summer;
			case 'autumn':
				// console.log("autumn");
				return season = this.state.clothes_autumn;
			case 'winter':
				// console.log('winter');
				return season = this.state.clothes_winter;
			default:
				return season = this.state.clothes_autumn;
		}
	}

	render() {
		return (
			<div className="main-wrapper">
				<div className='seasons-wrapper'>
					{this.state.seasons.map(item =>
						<CardSeason
							handlerSeason={this.handlerSeason}
							key={item.season}
							item={item}
						/>
					)}
				</div>

				<DianaGirlOuter
					key={'DianaGirlOuter'}
					season={this.getSeason()}
				/>
			</div>
		)
	}
}
