import React from 'react';

const CardClothes = (props) => {
	const { dress, index, handlerChangeDress } = props;
	return (
		<div key={'dress_num' + index} onClick={() => handlerChangeDress(index)}  >
			{dress.imgSrc ?
				<>
					< img src={dress.imgSrc} alt={dress.name} />
				</>
				:
				<button onClick={() => handlerChangeDress(index)}>Очистить</button>
			}
		</div>
	)

}
export default CardClothes;
